const fbxLoader = new FBXLoader();
fbxLoader.load("./earth/Cartoon Plane.fbx", (plane) => {
  plane.scale.x = 0.5 / 44;
  plane.scale.y = 0.5 / 44;
  plane.scale.z = 0.5 / 44;

  plane.position.z = 4;
  plane.position.y = 65.89;
  plane.rotation.x = 0.06503;
  plane.rotation.y = 7.65208;
  plane.castShadow = true;
//   gui.add(plane.position, "x", -80, 80, 0.01).name("Plane p X");
//   gui.add(plane.position, "y", -200, 80, 0.01).name("Plane p y");
//   gui.add(plane.rotation, "x", 0, 10, 0.00001).name("Plane r X");
//   gui.add(plane.rotation, "y", 0, 10, 0.00001).name("Plane r  Y");

//   const mouse = {
//     y: 65.89,
//   };
//   window.addEventListener("mousemove", (event) => {
//     mouse.x =event.offsetX /   0.06503;
//     mouse.y = event.offsetY /1000;
  
//     console.log(mouse.y,mouse.x);
//   });
  // animatingplane
  const clock = new THREE.Clock();
  const animatePlane = () => {
    const elapsedTime = clock.getElapsedTime();
    plane.rotation.x = Math.sin(elapsedTime) / 6;
    plane.position.x = Math.sin(elapsedTime) * 3;

    // plane.position.y = (mouse.y );
    // plane.position.x = (mouse.x);

    plane.traverse((child) => {
      if (child instanceof THREE.Mesh) {
        child.castShadow = true;
      }
    });
    window.requestAnimationFrame(animatePlane);
  };
  animatePlane();

  scene.add(plane);
});
fbxLoader.load("./earth/low_poly_earth.fbx", (earth) => {
  earth.position.y = -104.1;
  earth.rotation.x = 20;
  earth.scale.x = 1.6;
  earth.scale.y = 1.6;
  earth.scale.z = 1.6;
//   gui.add(earth.position, "x", -80, 80, 0.1).name("earth p X");
//   gui.add(earth.position, "y", -200, 80, 0.1).name("earth p y");
//   gui.add(earth.rotation, "x", -80, 80, 0.1).name("earth r X");
//   gui.add(earth.rotation, "y", -80, 80, 0.1).name("earth r y");

  earth.traverse((child) => {
    if (child instanceof THREE.Mesh) {
      child.receiveShadow = true;
    }
  });
  scene.add(earth);

  const clock = new THREE.Clock();
  let animation = () => {
    const elapsedTime = clock.getElapsedTime();
    earth.rotation.y = (elapsedTime / Math.PI) * 2;
    window.requestAnimationFrame(animation);
  };
  animation();
});

