let headOne= document.querySelector('.headOne')

text(headOne);

function text(elem) {
    let elementWidth = elem.offsetWidth;
    let windowWidth=  window.innerWidth;

    let flag=0;

    setInterval(() => {
        elem.style.marginLeft = --flag + "px";
  
        if (elementWidth == -flag) {
            flag = windowWidth;
        }
    }, 10);
}


